.App {
  text-align: center;
}
* {
  box-sizing: border-box;
  margin: 0;
  font-family: 'Inter', sans-serif;
}
.navbar{
  height: 55px;
  background: #F55A5A;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 7px;
}
.navbar > img{
  width: 24px;
  height: 24px;
}
.navbar > p{
  color: white;
}

.card{
  display: flex;
  gap: 19px;
  padding: 40px
}
.card-left >img{
  height: 168px;
  width: 125px;
  border-radius: 5px;
}
.location{
  display: flex;
  margin-bottom: 4px;
  margin-top: 25px;
}
.location > img{
  width: 7px;
  height: 9.55px;
  margin-right: 4px;
}
.location > p{
  margin-right: 12px;
  font-size: 10px;
}
.location > a{
  font-size: 10px;
  color: #918E9B;
}
.description{
  font-size: 10px;
}
.date{
  font-size: 10px;
  margin-bottom: 9px;       
  
}
.name{
  font-size: 25px; 
  margin-bottom: 7px;   
}

